<template>
	<div class="tits">
		<h1>{{titleName}}</h1>
		<div class="line"></div>
	</div>
</template>

<script>
	export default{
		name:'titleNavigation',
		props : {
			titleName : {
				required : true,
				type : String,
				default : ''
			},
		}
	}
</script>

<style scoped>
	.tits{
		width:100%;
		height:40px;
	}
	.tits {
		width: 100%;
		height: 40px;
		padding: 50px 0 4px;
		-webkit-box-sizing: content-box;
		box-sizing: content-box;
		background-color: #fff;
		border-bottom: 1px solid #ebebeb;
	}
	.tits h1 {
		margin: auto;
		font-size: 22px;
		text-align: center;
		color: #9f2e33;
		height: 100%;
		line-height: 32px;
		color: #595959;
		font-weight: 400;
	}
	.line{
		width:45px;
		height:1px;
		background:#e99014;
		margin:4px auto 0;
	}
</style>


